
ul{
    list-style: none;
  }
  a{
    text-decoration: none;
  }
  /*Navigation*/
  nav{
    top: 0;
    display: inline-flex;
    justify-content: space-around;
    align-items: center;
    height: 50px;
    width: 100%;
    position: fixed;
    z-index: 1;
    background: #ccc;
    box-shadow: 0px 2px 4px rgba(0,0,0,0.15), 
                0px 4px 8px rgba(0,0,0,0.15), 
                0px 8px 16px rgba(0,0,0,0.15), 
                0px 16px 32px rgba(0,0,0,0.15);
  }
  .logo a{
    font-family: "Roboto", sans-serif;
    font-weight: 600;
    color: #2c2c2c;
    font-size: 2rem;
  }
  .menu{
    display: flex;
  }
  .menu a{
    height: 40px;
    margin: 10px;
    line-height: 43px;
    padding: 0 40px;
    display: flex;
    font-size: 1rem;
    font-weight: 600;
    color: #2c2c2c;
    border-radius: 5px;
  }
  .menu a:hover{
    color: #000;
    background-color: #c0c0c0;
    box-shadow: inset 0px 2px 4px rgba(0,0,0,0.15), 
                inset 0px 4px 8px rgba(0,0,0,0.15), 
                inset 0px 8px 16px rgba(0,0,0,0.15);
    transition: 0.2s;
    transform: translateY(2px);
  }
  .main-text{
    /* position: absolute;
    left: 25%;
    top: 40%; */
    /* transform: translate(-13%,-42%); */
    color: #2c2c2c;
    width: 100%;
    height: 90%;
    display: flex;
    flex-direction: column;
  }
  .github{
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 40px;
  }
  .image{


    font-size: 70px;
  }
  .resume{
    margin: auto;
    display: grid;
  }
  .main-text h1{
    font-size: 3.5rem;
    text-align: center;
  }
  .main-text p{
    font-size: 2rem;
    margin: 25px 0px 25px;
    text-align: center;
    
  }
  .main {
    width: 100%;
    height: 30vh;
    
  }
  .typewriter{
    box-shadow: 0px 1px 2px 0px rgba(18, 18, 19, 0.7),
    1px 2px 4px 0px rgba(28, 28, 29, 0.7),
    2px 4px 8px 0px rgba(35, 35, 36, 0.7),
    2px 4px 16px 0px rgba(25, 25, 26, 0.7);
    text-align: center;
  
   
    
  }
  .typewriter>p{
    font-size: 20px;
  }
  .main_container{
    width: 75%;
    /* height:400px; */
    height: auto;
    margin: auto;
    display: grid;
    align-items: center;
    justify-content: space-between;
    grid-template-columns: repeat(2,1fr);
  }
  @media screen and (max-width: 600px){
    .main_container{
      grid-template-columns: repeat(1,1fr);
      /* gap: 100px */
      gap: 30px;
      /* margin-bottom: 100px; */

    }
    .image{
      font-size: 35px;
    }
    .resume{
      margin: auto;
      display: flex;
      justify-content: center;
      align-items: center;
      /* margin-bottom: 100px; */
      margin-top: 80px;

    }
}

/* medium sized devices */
@media screen and (max-width: 1024px){
  .main_container{
    grid-template-columns: repeat(1,1fr);

  }
  .resume{
    justify-content: center;
    align-items: baseline;
    margin-top: 50px;
    
  }
  .image{
    font-size: 45px;
  }
  nav{
    top: 0;
    display: inline-flex;
    justify-content: space-evenly;
    align-items: center;
    height: 50px;
    
  }
  .logo a{
    display: none;
  }
  
}