.calender_heading{
 
    text-align: center;
    margin-top: 20px;
    color: rgb(23, 210, 235);
    font-family: cursive;
        
        animation: blink 1s linear infinite;

}
.calender_container{
  width: 82%;
  margin: auto;
}
@keyframes blink{
    0%{opacity: 0;}
    50%{opacity: .5;}
    100%{opacity: 1;}
    }
   .stat{
    box-shadow: inset -5px -5px 9px rgba(255,255,255,0.45), inset 5px 5px 9px rgba(94,104,121,0.3);
   }
    .stat1{
      
      /* margin-top: 100px; */
      display: grid;
      padding: 10px;
      width: 72%;
      justify-content: center;
      align-items: center;
      grid-template-columns: repeat(2,1fr);
      margin: auto;
      gap: 10px;
     
      border-radius: 10px;


    }

    @media screen and (max-width: 600px){
      .calender_container{
        padding: 30px;

      }
      .stat1{
        grid-template-columns: repeat(1,1fr);
        align-items: center;
        width: 90%;

        justify-content: center;
      }



    }
    @media screen and (max-width: 1024px){
      .stat1{
        width: 85%;
      }
    }
    
